@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&family=Source+Serif+Pro:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  min-height: 100vh;
  /*background: #F2EFEB;*/
  /*background: #F2F2F2;*/
  /*background:#F8FBF8;*/
  --titleFont: 'Nanum Myeongjo', serif;
  --mainFont: 'Raleway', sans-serif;
  font-family: 'Raleway', sans-serif;
  --fblack:#050203;
  --fgrey: #A6A297;
  background: #f1f1f1;
  padding: 0 16vw;


}

.linkStyle {
  text-decoration: none;
  color: inherit;
  font-style: normal;
  letter-spacing: 0.02rem;
}
.linkStyle:hover {
  cursor: pointer;
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
  color: var(--pblack);
  border-color: var(--pline);
  transition: all 0.25s ease-out;
}

@media (max-width: 1500px) {
  .App {
    padding: 0 10%;
  }
}
@media (max-width: 700px) {
  .App {
    padding: 0 5%;
  }
}



.headbar{
    display:flex;
    flex-direction: column;
    justify-items: flex-end;
    align-items: center;
    color:var(--fblack);
    font-family: var(--titleFont);
    font-weight: bold;


}
.titleBar{
    width:100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    margin: 12px 0;
}
.chen{
    font-size: 24pt;
    letter-spacing: 1px;
    padding-left: 48px;
    color:var(--fgrey);

}
.title{
    position: relative;
    top:24px;
    font-size: 48pt;
    letter-spacing: 1px;
}
.bar{
    width:100%;
    border-style:solid;
    border-width: 0px 0 1px 0;
    display: inline-flex;
    flex-direction: row;
    padding: 2px 0;
    border-color:var(--fgrey);
    justify-content: flex-end;


}
.bar > *{
    padding: 0 0 4px 64px;
    font-size: 18px;
    /*border-width: 0 1.5px 0 0;*/
    letter-spacing: 1px;
    font-weight: bold;


}
